import React, { useEffect, useState } from 'react';
import { ProjectDetailContainer } from './ProjectDetailStyled';
import ProjectInfo from './PrjInfo/ProjectInfo';
import {
	FaShareSquare,
	FaFacebookSquare,
	FaTwitter,
	FaLine,
} from 'react-icons/fa';
import KakaoShareButton from '../../library/KakaoShareButton';
import axios from 'axios';
import {
	FacebookShareButton,
	TwitterShareButton,
	LineShareButton,
} from 'react-share';
import topviewEx from '../../Img/topviewEX.png';
import ReactHtmlParser from 'react-html-parser';
// import SubmitModal from './Modal/SubmitModal';
import SubmitModal from '../../component/Modals/Submit/SubmitModal';
import { useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';
import moment from 'moment';
import { TextFile } from '../../library/getJson';
function ProjectDetail(props) {
	const history = useHistory();
	const userInfo = useSelector((state) => state.userInfo);
	const challengeIdx = props.match.params.challengeIdx;
	const [challenge, setChallenge] = useState({});
	const [isDataReady, setIsDataReady] = useState(false);
	const [isShareActive, setIsShareActive] = useState(false);

	// 
	const [isSubmitOpen, setSubmitOpen] = useState(false);
	// console.log('challengeIdx: ', challengeIdx);
	// console.log('challenge: ', challenge);
	// console.log(props);
	const [comments, setComments] = useState([]);
	const [inputComment, setInputComment] = useState('');
	const handleChallenge = () => {
		//checkStatusCode
		// 1. 
		// 2. 
		// 3. 
		//4. 
		// 8. 
		//statusCode
		//1. 
		//2. 
		//3. 
		//4. 
		if (userInfo.email) {
			var config = {
				method: 'post',
				// https://u2-rest-dev.azurewebsites.net/api/Campaign/challengesubmit
				url:
					process.env.REACT_APP_U2_DB_HOST +
					`/Campaign/challengewished/${challengeIdx}`,
				headers: {
					Authorization: 'Bearer ' + localStorage.getItem('token'),
					'Content-Type': 'application/json',
				},
			};

			axios(config)
				.then((response) => {
					console.log('response: ');
					console.log(response.data);
					alert('  .');
				})
				.catch((err) => {
					console.log('err: ', err);
					alert('  .');
				});
		} else {
			if (
				window.confirm('  .  ?')
			) {
				history.push('/login');
			}
		}
	};

	const handleSubmit = () => {
		if (userInfo.email) {
			setSubmitOpen(true);
		} else {
			if (
				window.confirm('  .  ?')
			) {
				history.push('/login');
			}
		}
	};
	const handleApply = () => {};
	const handleModalClose = (modalType) => {
		switch (modalType) {
			case 'submit': {
				setSubmitOpen(false);
				break;
			}
			default: {
				console.log('no such case');
				break;
			}
		}
	};
	const handleCopyURL = () => {
		var dummy = document.createElement('input');
		var text = window.location.href;

		document.body.appendChild(dummy);
		dummy.value = text;
		dummy.select();
		document.execCommand('copy');
		document.body.removeChild(dummy);
		alert('URL .');
	};

	const handleComment = (reply) => {
		if (userInfo.email) {
			var body = {};
			// TextFile(body);
			if (reply) {
				body = {
					// commentIdx: 0,
					challengeIdx: Number(challengeIdx),
					seq: reply.seq,
					// seqx: 1,
					// memberIdx: userInfo.memberIdx,
					comment: comments[reply.index].inputReply,
					//   statusCode: 1,
					// "registMemberIdx": 0,
					// "registDate": "2021-07-14T11:00:22.230Z",
					// "modifyMemberIdx": 0,
					// "modifyDate": "2021-07-14T11:00:22.230Z"
				};
			} else {
				body = {
					// commentIdx: 0,
					challengeIdx: Number(challengeIdx),
					//   seq: 0,
					// seqx: 1,
					// memberIdx: userInfo.memberIdx,
					comment: inputComment,
					//   statusCode: 1,
					// "registMemberIdx": 0,
					// "registDate": "2021-07-14T11:00:22.230Z",
					// "modifyMemberIdx": 0,
					// "modifyDate": "2021-07-14T11:00:22.230Z"
				};
			}
			var config = {
				method: 'post',
				url: process.env.REACT_APP_U2_DB_HOST + `/Campaign/challengecomment`,
				headers: {
					Authorization: 'Bearer ' + localStorage.getItem('token'),
					'Content-Type': 'application/json',
				},
				data: body,
			};
			axios(config)
				.then((response) => {
					console.log('new comment: ', response.data);
					axios
						.get(
							process.env.REACT_APP_U2_DB_HOST +
								`/Campaign/challengecomments/${challengeIdx}`,
						)
						.then((response) => {
							console.log('comments: ', response.data);
							setInputComment('');
							setComments(response.data);
						});
				})
				.catch((err) => {
					console.log(err);
				});
		} else {
			if (
				window.confirm(` . 
			 ?`)
			) {
				history.push('/login');
			}
		}
	};

	useEffect(() => {
		axios
			.get(
				process.env.REACT_APP_U2_DB_HOST +
					`/Campaign/challenge/${challengeIdx}`, //sample data, should be challengeIdx.
			)
			.then((response) => {
				console.log('response.data: ', response.data);
				setChallenge(response.data);
				setIsDataReady(true);
			})
			.catch((err) => {
				console.log(err);
			});
		axios
			.get(
				process.env.REACT_APP_U2_DB_HOST +
					`/Campaign/challengecomments/${challengeIdx}`,
			)
			.then((response) => {
				console.log('comments: ', response.data);
				var comments = response.data;
				let newForm = comments.map((el) => {
					return { ...el, isReply: false };
				});
				setComments(newForm);
			});
	}, []);
	if (!isDataReady) {
		return <div>no data</div>;
	}
	return (
		<ProjectDetailContainer>
			<SubmitModal
				open={isSubmitOpen}
				challenge={challenge}
				handleModalClose={(modalType) => {
					handleModalClose(modalType);
				}}
			/>
			<section className="prj_title_area">
				<div className="prj_title">{challenge.title}</div>
				<div className="prj_term">
					{` : `}
					{moment(challenge.missions[0].dateBegin).format('YYYY-MM-DD')} ~{' '}
					{moment(challenge.missions[0].dateFin).format('YYYY-MM-DD')}
				</div>
			</section>
			<section className="prj-info">
				<ProjectInfo challenge={challenge}></ProjectInfo>
			</section>
			{/* <section className="prj_topview_area">
				<img src={topviewEx} alt="image" />
			</section> */}
			<section className="prj-detail">
				<div className="prj_content_wrap">
					{ReactHtmlParser(challenge.challengeDesc)}
				</div>
			</section>
			<section className="prj_control">
				<section className="prj_control_left">
					<div className="more_prj">
						{challenge.ownerName}   
					</div>
				</section>
				<section className="prj_control_middle">
					<button
						onClick={() => {
							history.push('/creatormarket');
						}}
					>
						
					</button>
					<button
						onClick={() => {
							if (userInfo.memberIdx) {
								handleChallenge();
							} else {
								if (window.confirm(' .')) {
									history.push('/login');
								}
							}
						}}
					>
						
					</button>
					{challenge.challengeTargetCode === 4 ? (
						<button
							onClick={() => {
								handleApply();
							}}
						>
							
						</button>
					) : (
						<button
							onClick={() => {
								handleSubmit();
							}}
						>
							
						</button>
					)}
				</section>

				<section className="prj_control_right">
					<div className="prjdetail_snsshare_area">
						<FaShareSquare
							id="prjdetail_sns_icon"
							onClick={() => {
								setIsShareActive(true);
							}}
						/>
						<span
							id="prjdetail_sns_text"
							onClick={() => {
								setIsShareActive(true);
								console.log(isShareActive);
							}}
						>
							
						</span>

						<div
							id="prjdetail_sns_share"
							className={
								isShareActive
									? 'pop_sub sns_share_pop active'
									: 'pop_sub sns_share_pop'
							}
						>
							<header>
								{' '}
								<div> </div>
								<img
									src="/img/closeBtn.svg"
									alt="X"
									onClick={() => {
										setIsShareActive(false);
									}}
								/>
							</header>

							<ul>
								<li>
									<div className="sns_img">
										{/* <img src="/img/ic_facebook.svg" /> */}
										<FacebookShareButton
											title={challenge.title}
											url={'https://u2-web-dev.azurewebsites.net'}
										>
											<img src="/img/ic_facebook.svg" />
										</FacebookShareButton>
									</div>
									<div className="sns_title"></div>
								</li>
								<li className="kakao_share">
									<div className="sns_img">
										<KakaoShareButton
											challengeTitle={challenge.title}
											imageUrl={'test'}
											// tags={['#test1', '#test2', '#test3']}
											// social={{
											// 	likeCount: 10,
											// 	commentCount: 23,
											// 	sharedCount: 333,
											// }}
											// buttons={[
											// 	{
											// 		title: 'button1',
											// 		link: {
											// 			mobileWebUrl: window.location.href,
											// 			webUrl: window.location.href,
											// 		},
											// 	},
											// 	{
											// 		title: 'button2',
											// 		link: {
											// 			mobileWebUrl: window.location.href,
											// 			webUrl: window.location.href,
											// 		},
											// 	},
											// ]}
										/>
									</div>
									<div className="sns_title"></div>
								</li>
								<li>
									<div className="sns_img">
										<TwitterShareButton
											title={challenge.title}
											url={'https://u2-web-dev.azurewebsites.net'}
										>
											<img src="/img/twittericon.svg" />
										</TwitterShareButton>
									</div>
									<div className="sns_title"></div>
								</li>
								<li
									onClick={() => {
										handleCopyURL();
									}}
								>
									<div class="sns_img">
										<img src="/img/ic_url_copy.svg" />
									</div>
									<div class="sns_title">URL</div>
								</li>
							</ul>
						</div>
					</div>

					{/* <SnsModal /> */}
				</section>
			</section>

			{/* <section>
        {' '}
        <KakaoShareButton
          challengeTitle={challenge.title}
          imageUrl={'test'}
          tags={['#test1', '#test2', '#test3']}
          social={{
            likeCount: 10,
            commentCount: 23,
            sharedCount: 333,
          }}
          buttons={[
            {
              title: 'button1',
              link: {
                mobileWebUrl: window.location.href,
                webUrl: window.location.href,
              },
            },
            {
              title: 'button2',
              link: {
                mobileWebUrl: window.location.href,
                webUrl: window.location.href,
              },
            },
          ]}
        />
        <FacebookShareButton
          title={challenge.title}
          url={'https://u2-web-dev.azurewebsites.net'}
        >
          <FaFacebookSquare />
        </FacebookShareButton>
        <TwitterShareButton
          title={challenge.title}
          url={'https://u2-web-dev.azurewebsites.net'}
        >
          <FaTwitter />
        </TwitterShareButton>
        <LineShareButton
          title={challenge.title}
          url={'https://u2-web-dev.azurewebsites.net'}
        >
          <FaLine />
        </LineShareButton>
      </section> */}
			<section className="commentInput">
				<textarea
					type="text"
					placeholder=" "
					onChange={(e) => {
						setInputComment(e.target.value);
					}}
				></textarea>
				<button
					onClick={() => {
						handleComment();
					}}
				>
					 
				</button>
			</section>
			<section className="comments">
				{comments &&
					comments.map((comment, idx) => {
						var copyArr = comments.slice();
						copyArr.splice(idx, 1); //    
						var replies = copyArr.filter(
							(reply) => reply.seq === comment.seq && reply.seqx !== 1,
						);

						if (comment.seqx !== 1) return; //     .
						return (
							<fieldset>
								<div className="comment_memberidx">
									<div>{comment.name}</div>
									<button
										onClick={() => {
											let copyArr = comments.slice();
											copyArr[idx].isReply = !copyArr[idx].isReply;
											setComments(copyArr);
										}}
									>
										
									</button>
								</div>
								<div className="comment_content">{comment.comment}</div>

								{replies.length > 0 &&
									replies.map((el) => {
										return (
											<div className="comment_reply">
												<img src="/img/replycomment.png" alt=" : " />
												<div className="comment_reply_area">
													<span className="comment_reply_name">
														{el.name ? el.name : ''}
													</span>
													<span className="comment_reply_content">
														{el.comment}
													</span>
												</div>
											</div>
										);
									})}
								<div
									className="comment_replyregi"
									style={{
										display: comments[idx].isReply ? 'grid' : 'none',
									}}
								>
									<input
										onChange={(e) => {
											comments[idx].inputReply = e.target.value;
										}}
									></input>
									<button
										onClick={() => {
											handleComment({ seq: comment.seq, index: idx });
										}}
									>
										
									</button>
								</div>
							</fieldset>
						);
					})}
			</section>
		</ProjectDetailContainer>
	);
}
export default ProjectDetail;
