import React, { useState } from 'react';
import axios from 'axios';
import requestBodyGenerator from '../../../../../library/requestBodyGenerator';
import { useSelector } from 'react-redux';
const Modal = ({
  modalOpen,
  handleOpenModal,
  handleCloseModal,
  header,
  handleNewData,
}) => {
  const [columnText, setColumnText] = useState('');
  const userInfo = useSelector(state => state.userInfo);
  const [ownerCat, setOwnerCat] = useState(0); // 0:  , 1: 
  const [company, setCompany] = useState('');
  const [logo, setLogo] = useState(null);
  const [email, setEmail] = useState('');
  const [mobile, setMobile] = useState('');
  const [sns, setSns] = useState('');
  const [snsType, setSnsType] = useState('F');
  const handleOwnerCat = e => {
    switch (e.target.value) {
      case '': {
        setOwnerCat(0);
        break;
      }

      case '': {
        setOwnerCat(1);
        break;
      }

      default:
        break;
    }
    // setOwnerCat(e.target.value);
  };
  const handleSubmit = () => {
    console.log(userInfo);
    const data = requestBodyGenerator(
      {
        ownerIdx: userInfo.memberIdx,
        memberIdx: userInfo.memberIdx,
        ownerCat: ownerCat,
        company: company,
        email: email,
        contact: mobile,
        socialMediaCode: snsType,
        socialMediaId: sns,
      },
      'challengeowner'
    );
    var config = {
      method: 'post',
      url: process.env.REACT_APP_U2_DB_HOST + '/Campaign/challengeowner',
      headers: {
        Authorization: 'Bearer ' + localStorage.getItem('token'),
        'Content-Type': 'application/json',
      },
      data: data,
    };
    axios(config)
      .then(response => {
        console.log(response.data);
        handleNewData(response.data);
        handleCloseModal();
      })
      .catch(err => {
        console.log(err);
      });
  };
  const handleColumnText = e => {
    setColumnText(e.target.value);
  };
  return (
    <div className={modalOpen ? 'openModal modal' : 'modal'}>
      {modalOpen ? (
        <section>
          <header>
            {header}
            <button className="close" onClick={handleCloseModal}>
              {' '}
              &times;{' '}
            </button>
          </header>
          <main>
            <section className="ele">
              <div className="menu"></div>
              <div className="inputInfo">
                <form className="selectForm">
                  <div>
                    <input
                      type="radio"
                      name="Form"
                      value=""
                      onClick={handleOwnerCat}
                      checked={ownerCat === 0}
                      readOnly
                    />
                    <label htmlFor="From"></label>
                  </div>
                  <div>
                    <input
                      type="radio"
                      name="Form"
                      value=""
                      onClick={handleOwnerCat}
                      checked={ownerCat === 1}
                      readOnly
                    />
                    <label htmlFor="From"></label>
                  </div>
                </form>
              </div>
            </section>
            <section className="ele">
              <div className="menu">{!ownerCat ? '' : ''}</div>
              <div className="inputInfo">
                <div className="inputCompany">
                  <input
                    type="text"
                    className="inputCompany"
                    onChange={e => {
                      setCompany(e.target.value);
                    }}
                  />
                </div>
              </div>
            </section>
            <section className="ele">
              <div className="menu">  </div>
              <div className="inputInfo">
                <div className="competitionName">
                  <input
                    type="file"
                    accept="image/*"
                    className="competitionName"
                    onChange={e => {
                      // console.log(e.target.value);
                      // console.log(e.target.files[0]);
                      setLogo(e.target.files[0]);
                    }}
                  />
                </div>
              </div>
            </section>
            <section className="ele">
              <div className="menu">* </div>
              <div className="inputInfo">
                <div className="inputEmail">
                  <input
                    type="text"
                    className="prjName"
                    onChange={e => {
                      setEmail(e.target.value);
                    }}
                  />
                </div>
              </div>
            </section>
            <section className="ele">
              <div className="menu">* </div>
              <div className="inputInfo">
                <div className="inputPhoneNumber">
                  <input
                    type="text"
                    className="prjName"
                    onChange={e => {
                      setMobile(e.target.value);
                    }}
                  />
                </div>
              </div>
            </section>
            <section className="ele">
              <div className="menu">SNS ID</div>
              <div className="inputInfo">
                <div className="inputSnsID">
                  <input
                    type="text"
                    className="prjName"
                    onChange={e => {
                      setSns(e.target.value);
                    }}
                  />
                  <select
                    onChange={e => {
                      console.log('snsType: ', e.target.value);
                      setSnsType(e.target.value);
                    }}
                  >
                    <option value="F" defaultChecked>
                      
                    </option>
                    <option value="K"></option>
                  </select>
                </div>
              </div>
            </section>
          </main>
          <footer>
            <button className="close" onClick={handleCloseModal}>
              {' '}
              {' '}
            </button>
            <button className="close saveBtn" onClick={handleSubmit}>
              {' '}
              {' '}
            </button>
          </footer>
        </section>
      ) : null}
    </div>
  );
};

export default Modal;
